body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
}

html {
  --accent-color: #00acdc;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
  display: block;
}

.header__logo img {
  object-fit: contain;
  width: 100%;
  height: 100%;
  display: block;
}

.head-nav__list {
  margin: 0;
  padding: 0;
  display: flex;
}

.head-nav__list :not(:first-child) {
  margin-left: 20px;
}

.head-nav__item {
  list-style: none;
}

.head-nav__link {
  color: #000;
  text-transform: uppercase;
  height: 60px;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  transition: color .2s;
  display: block;
  position: relative;
}

.head-nav__link.is-active, .head-nav__link:hover {
  color: var(--accent-color);
}

.head-nav__link.is-active:after {
  content: "";
  background: currentColor;
  border-radius: 8px;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
}

